:root {
  --active-color: #00acdc;
}

body {
  margin: 0;
}

html {
  font-family: Roboto, Helvetica Neue, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  margin: 0;
  padding: 0;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header__logo-pic {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}
